<template>
  <div class="in-views theater">
    <transition name="intoleft" appear>
      <h2 class="heading2">
        <transition name="intoleft" mode="out-in" appear>
          <p class="sub">劇場情報</p>
        </transition>
        <p>Theater</p>
      </h2>
    </transition>
    <div class="wrap-theater">
      <!-- ページ内リンク -->
      <div class="wrap-anchor">
        <!-- 地方をkeyにv-forで生成 -->
        <!-- スムーススクロールのためv-scrolltoを使用 -->
        <a  v-for="theaterList in theaterLists"
        v-scroll-to="{
          el: '#' + theaterList.theaterRegion,
          duration: 500,
          easing: 'ease-out',
          offset: -150,
        }"
        :key="theaterList.index"
        class="page-anchor">{{theaterList.theaterRegion}}
        <p class="angle">››</p> 
        </a>
      </div>
      <!-- 劇場情報全体を包む -->
      <div class="theater-index">
        <div class="top-spacer"></div>
        <!-- 地方をkeyにv-forで生成 -->
        <template v-for="theaterList in theaterLists">
          <!-- 地方ごとの表示を包む -->
          <div class="wrap-theater-info" :key="theaterList.index">
            <!-- 地方 -->
            <a :id="theaterList.theaterRegion" class="theater-region">{{theaterList.theaterRegion}}</a>
            <!-- <p class="theater-region">{{theaterList.theaterRegion}}</p> -->
            <table>
              <thead class="info-head">
                <td></td>
                <tr>
                  
                  <!-- 見出しをv-forで回して作成 -->
                  <th v-for="indexHeader in indexHeaders"
                  :key="indexHeader.index">
                    {{indexHeader}}
                  </th>
                  
                </tr>
              </thead>
              <tr class="table-spacer"></tr>
              <!-- 入れ子にしていた劇場情報をさらにv-forで表示 -->
              <tbody v-for="theaterInfo in theaterList.theaterIndex"
              :key="theaterInfo.theaterPref">
                <tr>
                  
                  <td>{{theaterInfo.theaterPref}}</td>
                  <td><a :href="theaterInfo.theaterUrl"> {{theaterInfo.theaterName}} </a></td>
                  <td>{{theaterInfo.theaterPhone}}</td>
                  <td>{{theaterInfo.theaterNote}}</td>
                  
                </tr>
              </tbody>
              <tr class="table-spacer"></tr>
            </table>
          </div>
        </template>
        <div class="bottom-spacer"></div>
      </div>
    </div>
  </div>
</template>

<script>

