<style scoped>

.wrap-theater{
  padding-top: 8.5vh;
  font-size: 1.5vh;
}

.theater-index{
  width: auto;
  color: black;
  background-color: rgba(255,255,255,.8);
  margin: 0 -6vw -2.5vh -6vw;
}

.wrap-theater-info{
  margin: 5vh auto 0 auto;
  border-bottom: 1px dotted rgba(0,0,0,.5);
}

.theater-region{
  font-size: 3vw;
  letter-spacing: 1vw;
  margin-top: 2vh;
}

.theater-region::after{
  content: "";
  margin-right: -1vw;
}

table{
  table-layout: fixed;
  width: 100%;
  border-collapse:collapse;
}

th{
  width: auto;
  color: white;
  font-weight: 100;
  background-color: rgba(100,100,100,1);
}

td{
  padding: 1vh 4vw;
}

a,
a:visited{
