}

.table-spacer{
  height: 1rem;
}

.width-spacer{
  width: 4vw;
}

.top-spacer{
  width: 100%;
  height: 1vh;
}

.bottom-spacer{
  width: 100%;
  height: 10vh;
}

/* ここからページ内リンク関連 */
.wrap-anchor{
  display: flex;
  justify-content: space-around;
  background-color: rgba(100,100,100,1);
  margin: 0 -6vw;
  padding: 2vh 6vw 1vh 6vw;
}

.page-anchor{
  font-size: 1.5vw;
  color: white;
  cursor: pointer;
}

.angle{
  transform: rotate(90deg) scale(1, 2) translateX(-0.5vh);
}

@media screen and (max-width:600px){
  .wrap-theater{
    padding-top: 25vh;
  }

  .wrap-anchor{
    justify-content: space-between;
  }

  .theater-index{
    margin: 0 -1.5vw calc(-2.5vh + -1.5vw) -1.5vw;
  }

  .wrap-anchor{
    margin: 0 -1.5vw;
  }
}

</style>
