import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    // 幅600pxを基準にしたメディアクエリを格納
    checkMatches: window.matchMedia('(max-width: 600px)'),
    // グローバルメニューの表示制御
    showMenu: true,
    // テーマカラー
    themeColor: '#16b8f3',
    // modalの背景
    modalOpacity: true,
    // 画像類
    imageAssets: [
      {
        id: 101,
        img: require('../assets/pic_staff_01.png'), 
        categoly: 'staff',
      },
      {
        id: 102,
        img: require('../assets/pic_staff_02.png'),
        categoly: 'staff',
      },
      {
        id: 201,
        img: require('../assets/pic_introduction_01.png'),
        categoly: 'introduction',
      },
      {
        id: 202,
        img: require('../assets/pic_introduction_02.png'),
        categoly: 'introduction',
      },
      {
        id: 203,
        img: require('../assets/pic_introduction_03.png'),
        categoly: 'introduction',
      },
      {
        id: 204,
        img: require('../assets/pic_introduction_04.png'),
        categoly: 'introduction',
      },
      {
        id: 301,
        img: require('../assets/kari-banner1.svg'),
        categoly: 'banner',
      },
      {
        id: 302,
        img: require('../assets/kari-banner2.svg'),
        categoly: 'banner',
      },
      {
        id: 303,
        img: require('../assets/kari-banner3.svg'),
        categoly: 'banner',
      },
      {
        id: 401,
        img: require('../assets/pic_comment_head.png'),
        categoly: 'comment',
      },
      {
        id: 501,
        img: require('../assets/Teaser_thumb.png'),
        categoly: 'trailerImg',
      },
      {
        id: 502,
        img: require('../assets/Teaser_soon.png'),
        categoly: 'trailerImg',
      },
      {
        id: 503,
        img: require('../assets/Teaser_thumb_temp.png'),
        categoly: 'trailerImg',
      },
      {
        id: 504,
        img: require('../assets/Poster.png'),
        categoly: 'trailerImg',
      },
      {
        id: 901,
        img: require('../assets/logo.svg'),
        categoly: 'logo',
      },
    ],
    // 動画類
    imageVideos: [
      {
        id: 1001,
        video: require('../assets/Teaser.mp4'),
        categoly: 'trailer',
      },
      {
        id: 9001,
        video: require('../assets/background.mp4'),
        categoly: 'background',
      },
    ],
  },
  mutations: {
    // checkMatchesを切り替えるミューテーション
    mutationsMatches(state){
      state.checkMatches = !state.checkMatches;
    },
    // showMenuを切り替えるミューテーション
    mutationsMenu(state){
      state.showMenu = !state.showMenu;
    },
    // modalOpacityを切り替えるミューテーション
    mutationsModal(state){
      state.modalOpacity = !state.modalOpacity;
    }
  },
  actions: {
    // mutationsMatchesを呼び出すアクション
    actionsMatches({commit}){
      commit('mutationsMatches');
    },
    // mutationsMenuを呼び出すアクション
    actionsMenu({commit}){
      commit('mutationsMenu');
    },
    // mutationsModalを呼び出すアクション
    actionsModal({commit}){
      commit('mutationsModal');
    }
  },
  modules: {
  }
})
