<template>
  <!-- 初回ロード画面に関するコンポーネント -->
  <transition name="load" appear>
    <div class="wrap-spinner">
        <div class="spinner"
        :style = "spinStyles">
          <div class="spin-core"></div>
        </div>
    </div>
    </transition>
</template>

<script>
// vue-loading-templateのインポート

export default {
  name: 'Spinner',
  computed:{
    spinStyles(){
      return{
        "background": 
          "linear-gradient(0deg, rgba(22, 184, 243, .1) 33%, rgba(22, 184, 243, 1) 100%)",
      }
    }
  }
}
</script>

<style scoped>
@keyframes spin {
  from {
    transform: rotate(0);
  }
  to{
    transform: rotate(359deg);
  }
}



/* ここからトランジションの設定 */
.load-enter,
.load-leave-to{
  opacity: 0;
}

.load-leave-active{
  transition: opacity 0s;
}

.load-leave-from,
.load-enter-to{
  opacity: 1;
}
/* ここまでトランジションの設定 */

.wrap-spinner{
  z-index: 9999;
    width: 100%;
  height: 100%;
  position: fixed;
  top: -8%;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: transparent;
}

.spinner {
  width: 80px;
  height: 80px;
  padding: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  animation: spin 1s linear 0s infinite;
}

.spin-core {
  width: 100%;
  height: 100%;
  background-color: rgb(0,0,0);
  border-radius: 50%;
}

</style>
