<template>
  <!-- ロゴを表示するためのコンポーネント -->
  <div class="logoarea" >
    <router-link to="/">
      <transition name="fade" key="views" mode="out-in" appear>
        <img v-show="showLogo" id="site-logo" alt="Site logo" src="../assets/logo.svg" type=”image/svg+xml”>
      </transition>
    </router-link>
  </div>
</template>

<script>
export default {
  // コンポーネント名をエクスポート
  name: 'LogoArea',
  data(){
    return{
      // 表示制御
      showLogo: true,
    }
  },
  methods:{
    scrollY(){
      // スクロール量を現在の
      this.currentTop = window.scrollY;
      if(!this.$route.path === '/' && !this.$store.state.checkMatches.matches && window.scrollY > 0){
        this.showLogo = false
      } else {
        this.showLogo = true
      }
    },
  },
  mounted(){
    window.addEventListener("scroll", this.scrollY);
  },
}
</script>

<style scoped>
  .logoarea{
    position: sticky;
    height: 7vh;
    width: 25vh;
    top: 7vh;
    padding: 0.5vh 0 0 6vw;
    z-index: 997;
  }
  #site-logo{
    width: 100%;
    min-width: 400px;
    height: auto;
    object-fit: contain;
    filter: drop-shadow(2px 2px 5px black);
  }

  @media screen and (max-width:1200px){
    #site-logo{
      min-width: 30vw;
    }
  }

  @media screen and (max-width:600px){
    .logoarea{
      position: absolute;
      width: 100%;
      height: auto;
      padding: 0 1.5rem;
      top: 10vh;
    }
    #site-logo{
      width: 100%;
      max-width: 100%;
      
    }
  }
</style>
