<template>
  <!-- ページトップにスクロールするボタンのコンポーネント -->
  <transition name="fade" appear>
    <!-- v-showで表示切り替え -->
    <div
    class="wrap-scroll"
    v-show="checkShowButton">
      <!-- @clickでスクロールイベント発動 -->
      <button
      class="scroll-top-button"
      @click="scrollTop">
        <angleUpIcon/>
      </button>
    </div>
  </transition>
</template>

<script>
import AngleUpIcon from '../atoms/AngleUpIcon.vue'

export default {
  name: 'PageTopButton',
  data(){
    return{
      //ボタン表示切り替え用変数
      checkShowButton: false,
    }
  },
  //mountedにてイベントリスナーに後述のボタン表示メソッドを登録し
  //条件をpointermoveに設定
  mounted() {
    window.addEventListener('pointermove', this.moveCheck);
  },
  methods: {
    //checkShowButtonをtrueに変更
    moveCheck(){
      this.checkShowButton = true;
    },
    //ページトップにスクロール
    scrollTop(){
      window.scrollTo({
        top: 0,
        behavior: 'smooth'
      });
    }
  },
  //checkShowButtonをwatchに登録
  watch:{
    checkShowButton:{
      handler(){
        //checkShowButtonがtrueの場合、5秒後にfalse
        if(this.checkShowButton){
          setTimeout(() => {
            this.checkShowButton = false;
          }, 5000);
        }
      }
    }
  },
  components:{
    AngleUpIcon
  }
}
</script>

<style scoped>
  .wrap-scroll{
    position: fixed;
    right: 6vw;
    bottom: 7vh;
    z-index: 5002;
    
  }

  .scroll-top-button{
    position: relative;
    background-color: rgba(0,0,0,.8);
    width: 60px;
    height: 60px;
    border:transparent;
    border-radius: 50%;
    color: #fff;
    font-size: 25px;
    text-align: center;
    padding: 0;
    cursor: pointer;
  }

</style>
