<template>
  <!-- ナビ関係を纏めたコンポーネント -->
  <div>
    <nav class="global-nav">
      <!-- ボタンクリックでshowMenuを切り替え -->
      <button id="hamburger"
        @click="changeShowMenu"
      >
        <HamburgerIcon/> 
      </button>
      
        <!-- showMenuを参照して表示の切り替え -->
        <div class="wrap-router" v-if="this.$store.state.showMenu">
          <router-link to="/">Top</router-link>
          <router-link to="/introduction">Introduction</router-link>
          <router-link to="/staff">Staff</router-link>
          <router-link to="/comment">Comment</router-link>
          <router-link to="/theater">Theater</router-link>
          <router-link to="/trailer">Trailer</router-link>
          <router-link to="/news">News</router-link>
        </div>
    </nav>
  </div>
</template>

<script>
import HamburgerIcon from '../atoms/HamburgerIcon.vue'

export default {
  name: 'GlobalMenu',
  data(){
    return{
      // ブレークポイントを格納する変数
      checkWidth: this.$store.state.checkMatches,
      // testWidth: this.$store.state.checkMatches,
    }
  },
  // mountedでmatchMediaを格納し、イベントリスナーに登録
  mounted(){
    // popStateMenuをイベントリスナーに登録
    window.addEventListener("popstate", this.popStateMenu);
    // nextTickで後述のmethodsを実行
    this.$nextTick(()=>{
      this.checkShowHam(this.checkWidth);
      this.checkWidth.addEventListener("change", this.checkShowHam);
      this.checkWidth.addEventListener("change", this.changeBreak);
    });
  },
  beforeDestroy(){
    // popstateのイベントリスナーを削除
    window.removeEventListener("popstate", this.popStateMenu);
    // console.log('remove event!!');
  },
  methods: {
    // ハンバーガーメニューの表示とスタイルの切り替え
    // v-showと@clickの併用はよくないらしいのでgetElementById.styleにて制御
    checkShowHam(checkWidth) {
      if (checkWidth.matches) {
      // ここから幅600px""""以下""""の場合
        // showMenuをfalseに設定
        this.$store.state.showMenu = false;
        // ハンバーガーメニュを表示
        document.getElementById('hamburger')
          .style.visibility = 'visible';
        // global-navの背景を透明にする
        document.getElementsByClassName('global-nav')[0]
          .style.backgroundColor = 'rgba(0,0,0,0)';
      } else {
      // ここから幅601px""""以上""""の場合
        // ハンバーガーメニュを非表示
        document.getElementById('hamburger')
          .style.visibility = 'hidden';
        // global-navの背景を黒にする
        document.getElementsByClassName('global-nav')[0]
          .style.backgroundColor = 'rgba(0,0,0,.8)';
        // showMenuをtrueに設定
        this.$store.state.showMenu = true;
      }
    },
    // 幅600px以下の場合、popstateの度にメニューを閉じる
    popStateMenu(){
      if(this.checkWidth.matches){
        this.$store.state.showMenu = false;
        // console.log('pop state!!');
      }
    },
    changeBreak(){
      this.$store.dispatch('actionsMatches');
    },
    changeShowMenu(){
      this.$store.dispatch('actionsMenu');
    }
  },
  /////////////////////////////////////////
  //// ここまでブレークポイント関係の処理 ////
  /////////////////////////////////////////

  // wrap-router表示中にbodyにlockクラスを追加するwatch
  watch:{
    showMenu:{
      handler(){
        if(this.$store.state.showMenu){
          document.body.className = 'lock';
        } else {
          document.body.className = '';
        }
      }
    }
  },
  components:{
    HamburgerIcon,
  }
}
</script>

<style scoped>
.global-nav {
  position: fixed;
  top: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 7vh;
  margin-bottom: 5vh;
  padding: 0 6vw;
  z-index: 5000;
  background-color: rgba(0,0,0,.8);
}

#hamburger{
  position: absolute;
  border: transparent;
  background: transparent;
  visibility: hidden;
  width: 35px;
  height: 35px;
  padding: 0;
  z-index: 5002;
}

.wrap-router{
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  width: 100%;
  height: auto;
  margin-left: 29vw;
  z-index: 5001;
}


.wrap-router a {
  color: #dce3eb;
  text-decoration: none;
  font-size: 1.3rem;
}

.wrap-router a.router-link-exact-active {
  color: #16b8f3;
}

/* 画面幅1200pxの設定 */
@media screen and (max-width:1200px){
  .wrap-router{
    margin-left: 10vw;
  }
  .wrap-router a {
    font-size: 1rem;
  }
}

/* 画面幅600pxの設定 */
@media screen and (max-width:600px){
  .global-nav{
    padding: 0;
    position: relative;
    margin: 0;
  }

.scroll-button-area{
    position: fixed;
    top: 2.5rem;
    right: 1.5rem;
  }

  #hamburger{
    margin: 1.5rem 1.5rem 0 0;
    right: 0;
    position: fixed;
  }

  .wrap-router{
    position: fixed;
    width: 100vw;
    height: 60vh;
    margin: 0;
    padding-top: 6vh;
    top: 0;
    background-color: rgba(6,6,6,1);
    flex-flow: column;
    justify-content: space-around;
  }

  .wrap-router a{
    width: 100%;
    font-size: 6vw;
  }
}

</style>
