<template>
<!-- <transition name="modalTransition" mode="out-in" appear> -->
  <div id="first-loading" v-if="preLoading">
    <!--  -->
    <div class="fullview">
      <Spinner v-if="showLoading" />
      <!-- プログレスバー全体 -->
      <div class="wrap-progress" v-if="showLoading">
        <!-- ロード進捗に合わせて伸びる要素 -->
        <div
          class="progress"
          :style="progressUp"
        ></div>
      </div>
    </div>
    <!-- メモリに置くため各要素を一度v-forで回す -->
    <div id="load-wrap">
      <!-- 画像には@loadイベントを配置 -->
      <img
        v-for="vForImage in vForImages"
        :key="vForImage.id"
        :src="vForImage.img"
        @load="loadCountUp"
      >
      <video
        v-for="vForVideo in vForVideos"
        :key="vForVideo.id"
        :src="vForVideo.video"
        muted="muted"
        autoplay
        controls
        class="load-video"
      ></video>
    </div>
    <!-- <div class="test-loading-bg"></div> -->
  </div>
<!-- </transition> -->
</template>

<script>
import Spinner from '../atoms/Spinner.vue';


export default {
  name: 'PreLoad',
  data(){
    return{
      // 現在のロード数を格納
      loadCounter: 0,
      // v-forで回す用の画像および動画をstoreから格納
      vForImages: this.$store.state.imageAssets,
      vForVideos: this.$store.state.imageVideos,
      // DOM生成後、videoタグを格納する
      videoTags: [],
      // スピンおよびプログレスバーの表示の制御
      showLoading: true,
      // 画面全体の表示の制御
      preLoading: true,
    }
  },
  methods:{
    // loadCounterに1加算する
    loadCountUp(){
      this.loadCounter++;
      // console.log('Image Loaded!!!!!!!!!!!!!!!!!');
    },
  },
  mounted(){
    // v-forで生成されたload-videoクラスの要素を格納
    this.videoTags = document.getElementsByClassName('load-video');
    // videoTagsの要素にそれぞれイベントリスナーを設定
    // 条件をもっと圧縮できないかと考えたがわかりやすさ重視
    for(let i = 0; i < this.videoTags.length; i++){
      this.videoTags[i].addEventListener('canplay', this.loadCountUp);
      this.videoTags[i].addEventListener('progress', this.loadCountUp);
      this.videoTags[i].addEventListener('loadeddata', this.loadCountUp);
      this.videoTags[i].addEventListener('suspend', this.loadCountUp);
    }
    window.onload = () => {
      // onloadが完了してからプログレスバーを100%にする
      this.loadCounter++;
      this.showLoading = false;
      // onloadイベント後、2秒カウントしてpreLoadingをfalseにし、
      // 3秒後にactionsModalをdispatch
      setTimeout(() => {
        this.preLoading = false;
        setTimeout(() => {
          // console.log('3秒!!!!!!!!!!!!!!!');
          this.$store.dispatch('actionsModal');
        }, 3000);
      }, 1500);
    };
    // 
  },
  computed:{
    // プログレスバーを動かし、cssに反映するためのcomputed
    progressUp(){
      // 読み込むアセットの長さ＝全体数を格納
      let length =
        (this.$store.state.imageAssets.length)
        +
        // 動画類にはイベントリスナーの数をかける
        (this.$store.state.imageVideos.length * 4)
      ;
      // console.log(this.loadCounter / length * 100);
      return {
        "background-color": this.$store.state.themeColor,
        // onload分を引く
        "width": (this.loadCounter - 1) / length * 100 + "%",
      }
    },
  },
  Destroyed(){
    for(let v = 0; v < this.videoTags.length; v++){
      this.videoTags[v].removeEventListener('canplay', this.loadCountUp);
      this.videoTags[v].removeEventListener('progress', this.loadCountUp);
      this.videoTags[v].removeEventListener('loadeddata', this.loadCountUp);
      this.videoTags[v].removeEventListener('suspend', this.loadCountUp);
    }
    // console.log('Remove!!!!!!!!!!!!!!!!');
  },
  components:{
    Spinner,
  },
}
</script>

<style scoped>
#first-loading{
  z-index: 9008;
}

.fullview {
  width: 100vw;
  height: 100vh;
  background: rgba(0,0,0,1);
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9006;
}



.load-wrap{
  z-index: 9005;
}

img,
video{
  width: 1%;
}

.wrap-progress{
  position: fixed;
  inset: 0;
  top: 10%;
  margin: auto;
  width: 50vw;
  height: 5px;
  border-radius: 10px;
  overflow: hidden;
  background-color: rgba(100,100,100,1);
}



.progress{
  height: 100%;
  
}
</style>
