import Vue from 'vue'
import VueRouter from 'vue-router'

import store from '@/store/index.js'
import Top from '../views/Top.vue'
import Introduction from '../views/Introduction.vue'
import Staff from '../views/Staff.vue'
import Comment from '../views/Comment.vue'
import Trailer from '../views/Trailer.vue'
import Theater from '../views/Theater.vue'
import News from '../views/News.vue'

Vue.use(VueRouter)


const routes = [
  {
    path: '/',
    name: 'top',
    component: Introduction
  },
  {
    path: '/introduction',
    name: 'introduction',
    component: Top
  },
  {
    path: '/staff',
    name: 'staff',
    component: Staff
  },
  {
    path: '/comment',
    name: 'Comment',
    component: Comment
  },
  {
    path: '/trailer',
    name: 'trailer',
    component: Trailer
  },
  {
    path: '/theater',
    name: 'theater',
    component: Theater,
  },
  {
    path: '/news',
    name: 'news',
    component: News
  },
  { // 存在しないURLの場合、Topにリダイレクト
    path:'*',
    redirect:'/'
  }
]

const router = new VueRouter({
  scrollBehavior (to, from, savedPosition) {
    if (savedPosition) {
      return savedPosition
    } else {
      return { x: 0, y: 0 }
    }
  },
  mode: 'history',
  base: '/shirakawa.github.io/03/',
  routes
})

// 画面幅が600px以下であった場合、ルート遷移時に
// showMenuをfalse。
router.afterEach(() => {
  Vue.nextTick(() => {
    if(store.state.checkMatches.matches){
      store.state.showMenu = false;
    }
  });
})


export default router
