<template>
  <div class="in-views introduction">
      <transition name="intoleft" appear>
        <h2 class="heading2">
          <transition name="intoleft" mode="out-in" appear>
            <p class="sub">イントロダクション</p>
          </transition>
          <p>Introduction</p>
        </h2>
      </transition>

      <!-- ここからスライド -->
      <div id="slide-area" ref="slideArea">
      <hooper
        id="intro-hooper"
        :settings="hooperSettings"
      >
        <!-- 1ページ目 -->
        <slide class="hooper-slide page1">
          <transition name="intoleft" mode="out-in" appear>
            <div class="slide-text-area">
              <p class="slide-text">
                吾輩は猫である。名前はまだ無い。どこで生れたかとんと見当がつかぬ。
                何でも薄暗いじめじめした所でニャーニャー泣いていた事だけは記憶している。
                吾輩はここで始めて人間というものを見た。
              </p>
            </div>
          </transition>
          
            <img
              class="slide-img"
              src="../assets/pic_introduction_01.png"
            >
          
        </slide>

        <!-- 2ページ目 -->
        <slide class="hooper-slide page2">
          <div class="slide-text-area">
            <p class="slide-text">
              木曾路はすべて山の中である。あるところは岨づたいに行く崖の道であり、
              あるところは数十間の深さに臨む木曾川の岸であり、
              あるところは山の尾をめぐる谷の入り口である。
              一筋の街道はこの深い森林地帯を貫いていた。
            </p>
          </div>
          <img
            class="slide-img"
            src="../assets/pic_introduction_02.png"
          >
        </slide>

        <!-- 3ページ目 -->
        <slide class="hooper-slide page3">
          <div class="slide-text-area">
            <p class="slide-text">
              しかし、私はあなたに喜びを非難し、痛みを賞賛するというこの誤った考えが
              どのように生まれたかを説明しなければなりません、そして私はあなたに
              システムの完全な説明を与え、真実の偉大な探検家、人間の幸福のマスタービルダー。
            </p>
          </div>
          <img
            src="../assets/pic_introduction_03.png"
            class="slide-img"
          >
        </slide>
        <!-- 4ページ目 -->
        <slide class="hooper-slide page4">
          <div class="slide-text-area">
            <p class="slide-text">
              ある日の暮方の事である。一人の下人が、羅生門の下で雨やみを待っていた。
              広い門の下には、この男のほかに誰もいない。ただ、所々丹塗の剥げた、大きな円柱に、
              蟋蟀が一匹とまっている。
            </p>
          </div>
          <img
            src="../assets/pic_introduction_04.png"
            class="slide-img"
          >
        </slide>
      <hooper-pagination slot="hooper-addons"/>
      <hooper-navigation slot="hooper-addons">
        <AngleUpIcon slot="hooper-prev"/>
        <AngleUpIcon slot="hooper-next"/>
      </hooper-navigation>
      </hooper>
      </div>
  </div>
</template>

<script>
import {
  Hooper,
  Slide,
  Pagination as HooperPagination,
  Navigation as HooperNavigation
} from 'hooper';

import AngleUpIcon from '../atoms/AngleUpIcon.vue'

export default {
  name: 'Introduction',
  data(){
    return{
      name:"",
      slideResizeCheck: window.matchMedia('(max-width: 600px)'),
      hooperSettings:{
        itemToShow: 1,
        infiniteScroll: true,
        wheelControl: false,
      }
    }
  },
  mounted(){
    this.$nextTick(()=>{
      this.slideSize();
      this.slideResizeCheck.addEventListener("change", this.slideSize);
    });
  },
  methods:{
    slideSize(){
      // views-wrapクラスを格納
      const setViewStyle = document.getElementsByClassName('views-wrap')[0];

      // views-wrapクラスのoffsetHeightを格納
      const viewAreaHeight = document.getElementsByClassName('views-wrap')[0].offsetHeight;
      
      // views-wrapクラスのCSSを格納
      const getViewStyle = window.getComputedStyle(setViewStyle);
      
      // 上記で取得したCSSからmargin-topを取得
      const slideMargin = getViewStyle.getPropertyValue('margin-top');
      
      // 取得したmargin-topの値を数値化
      const slideMarginMath = Number(slideMargin.replace(/px/g, ''));
      
      // global-navクラスの高さを格納
      const globalNavHeight = document.getElementsByClassName('global-nav')[0].offsetHeight;

      // views-wrapの高さ+global-navの高さ+views-wrapのmargin-top*2の数値を格納
      const slideAreaHeight = viewAreaHeight + globalNavHeight + (slideMarginMath);
      
      // slide-areaの高さをslideAreaHeightに設定
      document.getElementById('slide-area').style.height = slideAreaHeight + 'px';

    }
  },
  components:{
    Hooper,
    Slide,
    HooperPagination,
    HooperNavigation,
    AngleUpIcon,
  },
}
</script>

<style scoped>
/* hooperそのもののcssはhooper.cssに記述 */

#slide-area{
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 1;
}

#intro-hooper{
  width: auto;
  height: 100%;
}

.hopper-pagination{
  z-index: 4;
  position: absolute;
  top: 50%;
}

.slide-img{
  object-fit: cover;
  position: absolute;
  top: 0;
  width: auto;
  height: 100%;
  z-index: 2;
}

.slide-text-area{
  width: 50vw;
  height: auto;
  position: absolute;
  z-index: 3;
  /* leftかrightで設定すると要素が消えるのでtransform */
  transform: translate(40vw, 95vh);
}

.slide-text{
  font-size: 1.8vw;
  color: #fff;
  filter: drop-shadow(2px 2px 5px black);
  text-align: justify;
}

@media screen and (max-width:600px){
  .slide-text-area{
    transform: none;
    width: 100%;
    bottom: 10%;
    padding: 0 1.5vw;
  }
  .slide-text{
    font-size: 1rem;
  }
}

</style>
