<template>
  <div class="in-views trailer">
    <transition name="intoleft" appear>
      <h2 class="heading2">
        <transition name="intoleft" mode="out-in" appear>
          <p class="sub">トレイラー</p>
          </transition>
          <p>Trailer</p>
      </h2>
    </transition>

    <!-- トレイラーリスト全体を包む -->
    <div class="trailer-list">
      <!-- トレイラーをv-forで回す -->
      <template v-for="trailerList in reverseId">
        <div :key="trailerList.trailerId" class="trailer-area">
          
          
          <div class="trailer-thumb-area">
          <!-- 同じタグでなければv-forとv-ifは併用可能っぽい -->
            <!-- trailerMovieが存在する場合、imgをクリックするとshowTrailerを発火しtrailerIdを引数に渡す -->
              <img
                v-if="trailerList.trailerMovie"
                @click="showTrailer(trailerList.trailerId)"
                class="trailer-thumb-img"
                :src="trailerList.trailerThumb"
                :alt="trailerList.trailerTitle"
              >
              <img
                v-else
                class="trailer-thumb-null"
                :src="trailerList.trailerThumb"
                :alt="trailerList.trailerTitle"
              >
              <p class="trailer-title">
            {{trailerList.trailerTitle}}
          </p>
          </div>
          
        </div>
      </template>

        <!-- FirstViewModalのレイアウトを再利用 -->
        <modal name="trailer-modal"
          class="trailer-modal"
          :draggable="false"
          :resizable="false"
          :adaptive="true"
          :clickToClose="true"
          transition="modalTransition"
          overlayTransition="modalTransition"
          width="50%"
          height="50%"
          styles="
            background-color: rgba(0,0,0,0);
            border-radius: 0px;
            overflow: visible;
            box-shadow: 0 0px 0px 0 rgba(0,0,0,0);
            display: flex;
            flex-flow: column;
            align-items: center;
            justify-content: center;
            "
          >
    
      <!-- モーダルの内容物 -->
      <div class="trailer-modal-body">
        <!-- 動画部分 -->
          <!-- postTrailer内のmovieおよびthumbを表示 -->
          <video :src="postTrailer.trailerMovie"
            :poster="postTrailer.trailerThumb"
            controls
            preload="auto"
          ></video>
        <!-- ウィンドウを閉じるボタン -->
        <button class="trailer-close" @click="hideTrailer">
          <BatsuIcon/>
        </button>
      </div> 
      <div class="trailer-modal-bg"></div>
    </modal>

    </div>
  </div>
</template>

<script>
import BatsuIcon from '../atoms/BatsuIcon.vue'

export default {
  name: 'trailer',
  data() {
    return {
      // モーダルに表示する動画を格納する
      postTrailer: "",
      trailerLists: [
        {
          trailerId: 1,
          trailerTitle: '特報',
          trailerMovie: require('@/assets/Teaser.mp4'),
          trailerThumb: require('@/assets/Teaser_thumb.png'),
        },
        {
          trailerId: 2,
          trailerTitle: '※modal確認用仮設定',
          trailerMovie: require('@/assets/background.mp4'),
          trailerThumb: require('@/assets/Teaser_thumb_temp.png'),
        },
        {
          trailerId: 3,
          trailerTitle: 'TVCM 15秒',
          trailerMovie: null,
          trailerThumb: require('@/assets/Teaser_soon.png'),
        },
      ],
    }
  },
  // トレイラーモーダルの開閉
  methods: {
    // モーダルを開いた際にcheckTrailerIdにtrailerIdを受け取る
    showTrailer: function(checkTrailerId){
      // モーダルを開く
      this.$modal.show('trailer-modal');
      // trailerListsからtrailerIdとpostTrailerIdが一致するものをfindし、postTrailerに格納する
      this.postTrailer = this.trailerLists.find(checked => checked.trailerId === checkTrailerId);
    },
    // モーダルを隠す
    hideTrailer: function(){
      this.$modal.hide('trailer-modal');
    },
  },
  // v-forの内容を降順で表示
  computed: {
    reverseId(){
      return this.trailerLists.slice().reverse();
    },
  },
  components: {
    BatsuIcon,
  }
}
</script>

<style>
/* モーダル用トランジション */
.modalTransition-enter,
.modalTransition-leave-to{
  opacity: 0;
}

.modalTransition-leave-active{
  transition: opacity 1s;
}

.trailer-list{
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  height: auto;
  padding-top: 8.5vh;
}

.trailer-area{
  width: 50%;
  height: auto;
  margin: 3vh 0;
  padding: 0 2vw;
}

.trailer-title{
  font-size: 2vw;
  letter-spacing: 1vw;
  white-space: pre-line;
}

.trailer-title::after{
  content: "";
  margin-right: -1vw;
}

.trailer-thumb-area{
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,0);
}

.trailer-thumb-img,
.trailer-thumb-null{
  width: 100%;
  height: auto;
  object-fit: contain;
}

.trailer-thumb-img{
  cursor: pointer;
}

.trailer-thumb-null{
  cursor: default;
}

.trailer-modal-body{
  z-index: 6000;
  width: 100%;
  height: auto;
}

video{
  width: 100%;
  height: auto;
}

.trailer-close{
  position: absolute;
  width: 2em;
  height: 3em;
  top: 0;
  right: -45px;
  border: transparent;
  background: transparent;
  padding: 0;
  cursor: pointer;
}

.trailer-modal-bg{
  position: fixed;
  background-color: rgba(0,0,0,.9);
  width: 100%;
  height: 100%;
  z-index: 5999;
  overscroll-behavior: contain;
}

@media screen and (max-width:600px){
  .trailer-list{
    padding-top: 20vh;
  }

  .trailer-area{
    width: 100%;
  }
}

</style>
