<template>
  <div class="in-views comment">
    <transition name="intoleft" appear>
      <h2 class="heading2">
        <transition name="intoleft" mode="out-in" appear>
          <p class="sub">コメント</p>
          </transition>
          <p>Comment</p>
      </h2>
    </transition>

    <!-- コメントリスト全体を包む -->
    <div class="comment-list">
      <div class="comment-head-area">
        <p class="comment-head-text">
          数々の著名人から<br>
          絶賛の声！！
        </p>
        <img :src="commentHeadPic" alt="" class="comment-head-img">
      </div>
      <!-- コメントをv-forで回す -->
      <template v-for="commentList in commentLists">
        <div :key="commentList.commentId" class="comment-area">
          <div class="comment-text-area">
            <!-- 強調表示したいテキストにあわせてオブジェクトに入れる位置を変える -->
            <p class="emphasis-text">
              {{commentList.commentTextEmphasis1}}
            </p>
            <p class="comment-text">
              {{commentList.commentText1}}
            </p>
            <p class="emphasis-text">
              {{commentList.commentTextEmphasis2}}
            </p>
            <p class="comment-text">
              {{commentList.commentText2}}
            </p>
          </div>
          <!-- コメンターの名前および役職を包む -->
          <div class="commenter-name-area">
            <p class="commenter-name">
              {{commentList.commenterName}}
            </p>
            <p class="slash">／</p>
            <p class="commenter-prof">
              {{commentList.commenterProf}}
            </p>
          </div>
        </div>
      </template>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Comment',
  data() {
    return{
      commentHeadPic: require('@/assets/pic_comment_head.png'),
      commentLists: [
        {
          commenterId: 1,
          commenterName: '佐藤功',
          commenterProf: '俳優・タレント',
          commentTextEmphasis1: 'ゴーシュは前すましでくださらた。',
          commentText2: 'ばたばたアンコールはいって、なっが思っからしまっでして水車にすると音へどうしてぺん云いました。\n「顔来。水をふるえない。だまっぞ。きみはあれからこどもに来てじゃはじめ町はいいのなてぞ。」\nそこも気の毒そうとしてぞからだ意地悪をふくとだ町の眼に拭いて云いやつかてやろた。\n譜はやれて愕を聞えたた。やつもぱたっとかっこうはうるさいものたてつぶはまた高くんますで。',
        },
        {
          commenterId: 2,
          commenterName: '近藤正樹',
          commenterProf: 'ノンフィクション作家',
          commentText1: 'セロはその生意気んゴーシュみたいた先生にしながらざとじぶんのののへだまっでかぎをしてにわかにマッチにひるないんで、ばが居りいとだねずみでも教えるたゆうべこんどこすりまし所を前がドレミファをは耳め弾きましで。\n大きなかっこうひどくゆうべは誰か手ですなこわいものからこさえよ血人を入れて行くだです。用とくたびれてはそれはぎてしまいはもうじぶんがのかっこう室ましがなん起きあがっられたかっこう猫を、片手も何にたった二ぴきですて来て半分も病院のいっしょのその鳥に扉のセロがなったりねずみの眼を吹き出たりこすりてわらわすぎが出てどうもやりがやるけれどもやるなかっのまし。',
          commentTextEmphasis2: '顔、すこし大療でしと、ご人へふみまし。\nこれにシューマンのいちどを行くがごらんおどす。',
        },
        {
          commenterId: 3,
          commenterName: 'TATSURO',
          commenterProf: '歌手・ナチュラリスト',
          commentText1: 'するとぐっすりぱっと子をばかを弾いですだが孔は鳴ったましうた。ねどこもよしいつももういま何本のおねがいを叩きましてゴーシュを云いてるて勢たりゴーシュがおいおい子をしなた。すると一生けん命はセロをし屋をは大をは教えましたて頭する北の方目ですわり込んてぼくへすぐおいするとわあがっましなあというように見ひいやるたで。',
          commentTextEmphasis2: '舞台はまるで長いなりてどう次恐い弾いいたまし。',
          commentText2: '子しもトロメライをすって狸へ慈悲のように音のままをなるてきんへやってどう歩いましで。また、ぱちぱちおねがいどなりですというようにすこしやるましな。',
        }
      ],
    }
  },
}
</script>

<style>
@import url('https://fonts.googleapis.com/css2?family=Noto+Serif+JP:wght@200&display=swap');

.comment-list{
  display: flex;
  width: 100%;
  flex-flow: column;
  padding-top: 8.5vh;
}

.comment-head-area{
  display: flex;
  width: 100%;
  height: auto;
  justify-content: center;
  padding: 0 6vw;
  margin: 4vh auto;
}

.comment-head-text{
  font-size: 3.5rem;
  margin: auto 0;
  height: 100%;
  width: auto;
}

.comment-head-img{
  width: 320px;
  height: auto;
  object-fit: contain;
}

.comment-area{
  width: 80%;
  margin: 5vh auto;
  border-bottom: 1px solid #fff;
}

.comment-text-area{
  text-align: justify;
  padding-right: 8vw;
}

.emphasis-text,
.comment-text{
  white-space: pre-line;
}

.emphasis-text{
  font-size:2.5rem;
}

.comment-text{
  font-family: 'Noto Serif JP', serif;
  font-size: 1.2rem;
  
}

.commenter-name-area{
  display: flex;
  justify-content: flex-end;
  margin: 4vh 1rem 2vh 0;
}

.commenter-name{
  font-size: 2rem;
}

.slash,
.commenter-prof{
  align-self: flex-end;
  padding-bottom: .3rem;
}

.commenter-prof{
  font-size: 1.2rem;
}

.slash{
  font-size: 1.1rem;
  margin: 0 .5rem;
}

@media screen and (max-width:600px){
  .comment-list{
    padding-top: 20vh;
  }

  .comment-head-area{
    flex-wrap: wrap-reverse;
  }

  .comment-head-img{
    width: 60%;
  }
}
</style>
