<template>
  <div>
    <!-- vue-js-modalの表示および設定 -->
    <modal name="first-modal"
    class="first-modal"
    :draggable="false"
    :resizable="false"
    :adaptive="true"
    :clickToClose="false"
    transition="modalTransition"
    overlayTransition="modalTransition"
    width="100%"
    height="100%"
    styles="
      background: rgba(0,0,0,0);
      border-radius: 0px;
      box-shadow: 0 0px 0px 0 rgba(0,0,0,0);
    ">
    
      <!-- モーダルの内容物 -->
      <div class="modal-body">
        <!-- 動画部分 -->
        <video src="../assets/Teaser.mp4"
        poster="../assets/Teaser_thumb.png"
        controls="true"
        preload="auto"></video>
        <!-- ウィンドウを閉じるボタン -->
        <button class="modal-close" @click="hide">
          <BatsuIcon/>
        </button>
      </div>
    </modal>
    <transition name="modalTransition" mode="out-in" appear>
      <div class="modal-back-full" v-if="this.$store.state.modalOpacity"></div>
    </transition>
  </div>
</template>

<script>
import BatsuIcon from '../atoms/BatsuIcon.vue'

export default {
  // モジュール名をエクスポート
  name: 'FirstViewModal',
  data(){
    return{
      backOpacity: true,
    }
  },
  // 初回ロード時にモーダルを表示する
  mounted(){
    this.$modal.show('first-modal');
    // モーダル表示時にbodyにクラスを追加
    document.body.className = 'lock';
    // ロード完了時(今回は指定秒数経過)でLoading.vueを消す
  },
  // モーダルを閉じるメソッド
  methods: {
    hide : function () {
      this.$modal.hide('first-modal');
      // モーダルを閉じた際にbodyのクラスを削除
      document.body.className = '';
    }
  },
  components:{
    // Loading,
    BatsuIcon
  }
}
</script>

<style scoped>
/* モーダル用トランジション */
.modalTransition-enter,
.modalTransition-leave-to{
  opacity: 0;
}

.modalTransition-leave-active{
  transition: opacity 1.5s;
}


.first-modal{
  background-color: rgba(0,0,0,.9);
  width: 100%;
  height: 100%;
  z-index: 8001;
  
}

.modal-body{
  z-index: 6000;
  display: flex;
  flex-flow: column;
  align-items: center;
  justify-content: center;
  width: 50%;
  height: auto;
  position: relative;
  top: 25%;
  margin: auto;
}

video{
  width: 100%;
  height: auto;
}

.modal-close{
  position: fixed;
  width: 2em;
  height: 3em;
  top: 20%;
  right: 20%;
  border: transparent;
  background: transparent;
  padding: 0;
  cursor: pointer;
}

.modal-back-full{
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0,0,0,1);
  z-index: 8000;
}

@media screen and (max-width:600px){
  .modal-body{
    width: 90%;
  }

  .modal-close{
    top: 15%;
    right: 10%;
  }
}
</style>
